export default {
	data() {
		return {};
	},
	methods: {
		onSubmit(e) {
			this.$Fetch({
				url: 'advise/add',
				data: e.detail.value,
				success: res => {
					uni.showToast({
						title: '提交成功！',
						icon: 'success'
					});
					setTimeout(() => {
						uni.navigateBack({});
					}, 1500);
				}
			});
		}
	}
};
